html, body {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root, #root > div {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flex-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.header-container {
    width: 100%;
    height: 100%;
}

.header-container__form {
    justify-content: center;
    margin-right: 10px;
    height: 30%;
    border-right: 2px solid DodgerBlue;
}

.header-container__form-button {
    padding: 10px;
    margin: 0 10px;
    background-color: DodgerBlue;
    color: #fff;
    font-size: 16px;
    border: 1px solid transparent;
    border-radius: 10px;
    cursor: pointer;
    outline: none;
}

.header-container__form-button:hover {
    background-color: yellowgreen;
}

.header-container__autocomplete {
    position: relative;
    display: inline-block;
}

.header-container__autocomplete-inputcontainer {
    border: 3px solid DodgerBlue;
}

.header-container__autocomplete-icon {
    padding: 0 5px 0 10px;
    color: DodgerBlue;
}

.header-container__autocomplete-input {
    width: 300px;
    position: relative;
    padding: 10px;
    font-size: 16px;
    border: none;
    outline: none;
}

.header-container__autocomplete-list {
    position: absolute;
    display: inline-block;
    top: 100%;
    left: 0;
    right: 0;
    border-bottom: none;
    border-top: none;
    z-index: 99;
}

.header-container__autocomplete-list div {
    position: relative;
    padding: 10px;
    background-color: #fff; 
    border-bottom: 1px solid #d4d4d4; 
    cursor: pointer;
}


.header-container__autocomplete-list div:hover {
    background-color: #e9e9e9; 
}

.header-container__github {
    margin-right: 10px;
    background-color: transparent;
    font-size: 16px;
    border: none;
    outline: none;
    cursor: pointer;
}

.header-container__github-icon {
    font-size: 500%;
}
.loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}
.flex-container__mentorsroom {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.mentors-room-container {
    height: 100%;
    width: 100%;
}

.mentors-room__button-container {
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    height: 10%;
}


.mentors-room__button {
    margin: 4%;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
}

.mentors-room__table {
    height: 40%;
    width: 80%;
    text-align: center;
    border: 1px solid #ddd;
    border-collapse: collapse;
}

.mentors-room__table-body td {
    text-align: start;
}

.mentors-room__table-header td, 
.mentors-room__table-body td {
    border: 2px solid #ddd;
    cursor: pointer;
}

.mentors-room__table-header td a, 
.mentors-room__table-body td a  {
    display: block;
    padding: 3% 3%;
    color: black;
    text-decoration: none;
} 

.checked-by-mentor {
    background-color: rgb(217,234,211);
}

.todo-status {
    background-color: rgb(204,204,204);
}

.in-process {
    background-color: rgb(255,242,204);
}

.need-to-check {
    background-color: rgb(234,153,153);
}

.not-performed {
    background-color: rgb(166,28,0);
}
