.flex-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.header-container {
    width: 100%;
    height: 100%;
}

.header-container__form {
    justify-content: center;
    margin-right: 10px;
    height: 30%;
    border-right: 2px solid DodgerBlue;
}

.header-container__form-button {
    padding: 10px;
    margin: 0 10px;
    background-color: DodgerBlue;
    color: #fff;
    font-size: 16px;
    border: 1px solid transparent;
    border-radius: 10px;
    cursor: pointer;
    outline: none;
}

.header-container__form-button:hover {
    background-color: yellowgreen;
}

.header-container__autocomplete {
    position: relative;
    display: inline-block;
}

.header-container__autocomplete-inputcontainer {
    border: 3px solid DodgerBlue;
}

.header-container__autocomplete-icon {
    padding: 0 5px 0 10px;
    color: DodgerBlue;
}

.header-container__autocomplete-input {
    width: 300px;
    position: relative;
    padding: 10px;
    font-size: 16px;
    border: none;
    outline: none;
}

.header-container__autocomplete-list {
    position: absolute;
    display: inline-block;
    top: 100%;
    left: 0;
    right: 0;
    border-bottom: none;
    border-top: none;
    z-index: 99;
}

.header-container__autocomplete-list div {
    position: relative;
    padding: 10px;
    background-color: #fff; 
    border-bottom: 1px solid #d4d4d4; 
    cursor: pointer;
}


.header-container__autocomplete-list div:hover {
    background-color: #e9e9e9; 
}

.header-container__github {
    margin-right: 10px;
    background-color: transparent;
    font-size: 16px;
    border: none;
    outline: none;
    cursor: pointer;
}

.header-container__github-icon {
    font-size: 500%;
}