.flex-container__mentorsroom {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.mentors-room-container {
    height: 100%;
    width: 100%;
}

.mentors-room__button-container {
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    height: 10%;
}


.mentors-room__button {
    margin: 4%;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
}

.mentors-room__table {
    height: 40%;
    width: 80%;
    text-align: center;
    border: 1px solid #ddd;
    border-collapse: collapse;
}

.mentors-room__table-body td {
    text-align: start;
}

.mentors-room__table-header td, 
.mentors-room__table-body td {
    border: 2px solid #ddd;
    cursor: pointer;
}

.mentors-room__table-header td a, 
.mentors-room__table-body td a  {
    display: block;
    padding: 3% 3%;
    color: black;
    text-decoration: none;
} 

.checked-by-mentor {
    background-color: rgb(217,234,211);
}

.todo-status {
    background-color: rgb(204,204,204);
}

.in-process {
    background-color: rgb(255,242,204);
}

.need-to-check {
    background-color: rgb(234,153,153);
}

.not-performed {
    background-color: rgb(166,28,0);
}